.menu-drop-down {
  background: white;
  position: absolute;
  z-index: 100;
  padding: 4px;
  border: solid #aaa 1px;
  top: 100%;
  left: 1px;
  display: none;
}

.menu-drop-down.open {
  display: block;
}

.menu-drop-down li {
  padding: 2px 3px;
  font-size: 13rem;
  font-weight: normal;
  vertical-align: middle;
  min-width: 150rem;
}

.menu-drop-down li:hover {
  background-color: #eee;
}

.r-tool-bar {
  position: relative;
  width: 100%;
  height: 100%;
  padding: 6px 5px;
}

.r-tool-bar ul.r-tool-bar-items li {
  display: inline-block;
  position: relative;
  font-size: 13rem;
  margin-left: 4px;
  /*& .tool-bar-menu{

            }

            & div.tool-bar-text{
                font-size: 14rem;
                font-family: Calibri, Arial, sans-serif;
                font-weight: 500;
                position: relative;
                display: inline-block;
                height: 26rem;
                line-height: 26rem;
                text-align: center;
                cursor: pointer;
                border: 0;
                outline: none;

                & .bar-icon{
                    position: relative;
                    left: -2px;
                    top: 1px;
                }
            }*/
}

.r-tool-bar ul.r-tool-bar-items li .loading {
  position: absolute;
}

.r-tool-bar ul.r-tool-bar-items li .bar-btn-load {
  position: relative;
  display: inline-block;
  top: -16px;
  left: 3px;
}

.r-tool-bar ul.r-tool-bar-items li.right {
  float: right;
  border: none;
}

.r-tool-bar ul.r-tool-bar-items li .tool-bar-menu {
  color: white;
  background: linear-gradient(to bottom, #2398c5, #057ba8);
  min-width: 90rem;
  padding: 0 12rem;
  border-radius: 0;
  height: 26rem;
  /*& .tool-bar-menu-down{
                    padding-right: 3rem;
                    padding-left: 3rem;
                    border-left: solid 1px transparent;
                    border-right: solid 1px transparent;
                    display:inline-block;
                    position:relative;
                }*/
}

.r-tool-bar ul.r-tool-bar-items li .tool-bar-menu.disabled {
  background: #dddddd;
  color: rgba(0, 0, 0, 0.26);
  pointer-events: none;
  cursor: auto;
}

.r-tool-bar ul.r-tool-bar-items li .tool-bar-menu .menu-drop-down {
  right: 0%;
  left: inherit;
  width: 100%;
  color: black;
  padding: 0;
}

.r-tool-bar ul.r-tool-bar-items li .tool-bar-menu .menu-drop-down li {
  width: 100%;
  min-width: 0;
  margin: 0;
  padding: 0;
}

.r-tool-bar ul.r-tool-bar-items li .tool-bar-menu .menu-drop-down li .items {
  padding: 3px 4px;
  display: block;
  color: black;
}

.r-tool-bar ul.r-tool-bar-items li .tool-bar-menu .tool-bar-text {
  font-size: 14rem;
  font-family: Calibri, Arial, sans-serif;
  font-weight: 500;
  position: relative;
  display: inline-block;
  height: 26rem;
  line-height: 26rem;
  text-align: center;
  cursor: pointer;
  border: 0;
  outline: none;
}

.r-tool-bar ul.r-tool-bar-items li .tool-bar-menu:after {
  content: "";
  display: inline-block;
  width: 0;
  height: 0;
  margin-left: 7px;
  vertical-align: middle;
  border-top: 4px dashed;
  border-top: 4px solid \9;
  border-right: 4px solid transparent;
  border-left: 4px solid transparent;
}




