.select-none {
  cursor: default;
}

.main-drop-down {
  width: 100%;
  position: relative;
}

.main-drop-down.error .drop-down-arrow {
  border-top-color: red;
  border-bottom-color: red;
}

.main-drop-down.error input {
  border-color: red;
}

.main-drop-down.disabled {
  opacity: 0.3;
}

.main-drop-down.disabled .drop-down-overlay {
  position: absolute;
  background: rgba(0, 0, 0, 0);
  width: 100%;
  height: 100%;
  z-index: 9999;
  cursor: default;
}

.main-drop-down input:disabled {
  background: white;
}

.main-drop-down .main-loading {
  position: absolute;
  width: 100%;
  left: 0;
  top: 100%;
  padding-left: 33%;
  border: 1px solid rgba(0, 0, 0, 0.15);
  -webkit-box-shadow: 0 6px 12px rgba(0, 0, 0, 0.175);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.175);
  padding-top: 1px;
}

.main-drop-down.open .drop-down {
  display: block;
}

.main-drop-down .drop-down {
  top: 100%;
  display: none;
  position: absolute;
  margin: 0;
  background: #fff;
  color: black;
  overflow-y: auto;
  max-height: 350px;
  min-width: 100%;
  z-index: 995;
  overflow-x: hidden;
  border: 1px solid rgba(0, 0, 0, 0.15);
  -webkit-box-shadow: 0 6px 12px rgba(0, 0, 0, 0.175);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.175);
}

.main-drop-down .drop-down ul {
  position: relative;
}

.main-drop-down .drop-down ul li {
  padding: 3px 3px 3px 5px;
  border-bottom: 1px solid #efefef;
  cursor: pointer;
  white-space: nowrap;
}

.main-drop-down .drop-down ul li.active {
  background: rgba(253, 231, 119, 0.3);
}

.main-drop-down .drop-down ul li:hover {
  background-color: #efefef;
}

.main-drop-down .label-clear .drop-down-arrow {
  right: 22rem;
}

.main-drop-down .drop-down-arrow {
  background: linear-gradient(#f9f9f9 0%, #e2e2e2 100%);
  position: absolute;
  right: 0;
  top: 0;
  border: 1px solid #8bb0bf;
  padding: 6rem;
  height: 100%;
  cursor: pointer;
}

.main-drop-down .drop-down-arrow:after {
  margin-top: -9px;
  content: "";
  display: inline-block;
  width: 0;
  height: 0;
  margin-left: 0;
  vertical-align: middle;
  border-top: 4px dashed;
  border-top: 4px solid \9;
  border-right: 4px solid transparent;
  border-left: 4px solid transparent;
}

.main-drop-down .label-clear input {
  padding-right: 50rem !important;
}




