.page-nav {
  clear: both;
  background: #fafafa;
  padding: 5px;
  border-bottom: 1px solid #e6e6e6;
  height: 40px;
}

.page-nav .page-info {
  display: block;
  float: right;
  margin-top: 7px;
  margin-right: 16px;
}

.page-nav ul {
  padding: 0;
  margin: 0;
  border: 0;
  outline: 0;
}

.page-nav ul li {
  display: inline;
  cursor: pointer;
}

.page-nav ul li span {
  margin-top: -.4em;
  padding-top: .35em;
  border-color: #3f51b5 rgba(0, 0, 0, 0) rgba(0, 0, 0, 0);
  border-radius: 0;
  box-shadow: none;
  display: inline-block;
  text-decoration: none;
  color: #444;
  padding: 0;
  min-width: 2.429em;
  height: 2.429em;
  line-height: 2.429em;
  border-width: 2px 0 0;
  text-align: center;
  border-top: 2px solid #fafafa;
}

.page-nav ul li span.active {
  border-color: #007797;
  color: #007797;
}




