.dialog {
  display: flex;
  max-width: 96vw;
  max-height: 96vh;
  flex-direction: column;
  background-color: white;
  border-radius: 2rem;
  box-shadow: 0 19px 60px rgba(0, 0, 0, 0.3), 0 15px 20px rgba(0, 0, 0, 0.22);
  opacity: 0;
  transition-delay: 0.07s;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 0.35s;
  transition-property: opacity, transform;
  transform: translateY(-40rem);
}

.dialog.active {
  opacity: 1;
  transform: translateY(0%);
}

@media screen and (max-height: 875px) {
  #mediatop {
    display: block;
  }
}

.dialog-form {
  position: absolute;
  z-index: 1000;
  background-color: white;
  border-radius: 2rem;
  box-shadow: 0 19px 60px rgba(0, 0, 0, 0.3), 0 15px 20px rgba(0, 0, 0, 0.22);
  opacity: 0;
  transition-delay: 0.07s;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 0.35s;
  transition-property: opacity, transform;
  /*transform: translateY(-$dialog-translate-y);*/
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(0%);
  /*-webkit-transform: translateX(-50%) translateY(-50%);
    -moz-transform: translateX(-50%) translateY(-50%);
    -ms-transform: translateX(-50%) translateY(-50%);
    transform: translateX(-50%) translateY(-50%);*/
}

.dialog-form .g-h-title {
  margin-top: 18px;
  margin-bottom: 4px;
}

.dialog-form .g-dialog-form .main-field {
  padding-bottom: 7px;
}

.dialog-form.fixed {
  position: fixed;
  left: 50% !important;
  top: 50% !important;
  transform: translate(-50%, -50%) !important;
}

.dialog-form.active {
  opacity: 1;
  /*transform: translateY(0%);*/
}

.dialog-form .dialog-actions {
  background: #f6f7f8;
  padding: 5px;
  text-align: right;
}

.dialog-form .dialog-actions button {
  margin-left: 8px;
}

.dialog-form .dialog-actions.top {
  display: none;
  position: absolute;
  top: 0;
  right: 0;
  background: none;
}

.dialog-form.is-loaded {
  display: none;
}

.dialog-form .dialog-body {
  margin: 0 24rem 24rem 24rem;
}

.dialog-form .dialog-body table {
  border: 1px solid #ccc;
}

.small {
  width: 30vw;
}

@media screen and (max-width: 720px) {
  .small {
    width: 50vw;
  }
}

@media screen and (max-width: 600px) {
  .small {
    width: 75vw;
  }
}

.normal {
  width: 50vw;
}

@media screen and (max-width: 600px) {
  .normal {
    width: 96vw;
  }
}

.large {
  width: 96vw;
}

.title {
  font-family: Calibri, Arial, sans-serif;
  font-size: 20rem;
  font-weight: 500;
  line-height: 1;
  letter-spacing: .02em;
  flex-grow: 0;
  margin-bottom: 16rem;
  color: black;
}

.body {
  padding: 24rem;
}

