.ripple {
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 100;
  pointer-events: none;
  background-color: currentColor;
  border-radius: 50%;
  transform-origin: 50% 50%;
}

.rippleWrapper {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1;
  pointer-events: none;
  overflow: hidden;
}

.ripple {
  transition-duration: 800ms;
}

.ripple.rippleRestarting {
  opacity: 0.3;
  transition-property: none;
}

.ripple.rippleActive {
  opacity: 0.3;
  transition-property: transform;
}

.ripple:not(.rippleActive):not(.rippleRestarting) {
  opacity: 0;
  transition-property: opacity, transform;
}




