@font-face {
  font-family: Myriad;
  src: url("./fonts/MyriadPro/MyriadPro-Regular.ttf") format("truetype");
}

html {
  font-size: 1px;
}

body {
  position: absolute;
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
  font: 14rem Calibri, Arial, sans-serif;
  -webkit-touch-callout: none;
}

body * {
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
}

a,
abbr,
address,
article,
aside,
audio,
b,
blockquote,
body,
caption,
cite,
code,
dd,
del,
dfn,
dialog,
div,
dl,
dt,
em,
fieldset,
figure,
footer,
form,
h1,
h2,
h3,
h4,
h5,
h6,
header,
hgroup,
hr,
html,
i,
iframe,
img,
ins,
kbd,
label,
legend,
li,
mark,
menu,
nav,
object,
ol,
p,
pre,
q,
samp,
section,
small,
span,
strong,
sub,
sup,
table,
tbody,
td,
tfoot,
th,
thead,
time,
tr,
ul,
var,
video {
  padding: 0;
  margin: 0;
  border: 0;
  outline: 0;
}

input:not([type="checkbox"]):not([type="radio"]),
button {
  outline: none;
  appearance: none;
  -webkit-touch-callout: none;
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
}

.clear-x::before {
  display: table;
  content: " ";
}

.clear-x::after {
  clear: both;
  display: table;
  content: " ";
}

.head {
  display: flex;
  flex: 1;
  flex-direction: row;
  height: 104px;
  background: url("data:image/png;base64,") no-repeat;
  background-position: right top;
  background-color: white;
}

ul,
ol {
  list-style: none;
}

.logo {
  width: 100%;
  height: 104px;
  background: url("./images/logo12.png") no-repeat;
}

.tabs {
  border-bottom: 1px solid #d2d2d2;
}

.tab {
  cursor: pointer;
  padding: 0 24px 0px 24px;
  color: #777777;
}

.tabs .tab:not(:first-child) {
  border-left: 1px solid #d2d2d2;
}

.tab.selected {
  color: black;
  font-weight: 600;
  border-bottom: 2px solid black;
  height: 28px;
}

.card {
  box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.14), 0 1px 1px 0 rgba(0, 0, 0, 0.12), 0 1px 1px -2px rgba(0, 0, 0, 0.2);
  border-radius: 1px;
  background-color: white;
  overflow: auto;
}

.card.filter {
  background: #c8cdd3;
}

.padding {
  padding: 24px;
}

.input {
  border: 1px solid #5090cd;
  border-radius: 2px 0px 0px 2px;
  font-family: inherit;
  font-size: inherit;
  padding: 1px 3px;
  height: 26px;
}

.dropdown {
  height: 26px;
  border: 1px solid #5090cd;
}

.clear {
  margin: 0;
  width: 27px;
  height: 26px;
  background-color: #f5f5f7;
  border-top: 1px solid #5090cd;
  border-right: 1px solid #5090cd;
  border-bottom: 1px solid #5090cd;
  border-left: 0 solid #5090cd;
  border-radius: 0 2px 2px 0;
}

.textarea {
  border: 1px solid #5090cd;
  font-family: inherit;
  font-size: inherit;
  resize: none;
  margin-top: 6px;
  border-radius: 2px;
}

.add-button {
  padding-right: 24px;
  padding-left: 19px;
  border-radius: 6px;
  font-size: inherit;
  background-color: #0055a6;
  background: linear-gradient(to right, #0055a6, #2672bd);
  color: white;
  cursor: pointer;
  height: 30px;
  box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.14), 0 1px 1px 0 rgba(0, 0, 0, 0.12), 0 1px 1px -2px rgba(0, 0, 0, 0.2);
}

.printer-button {
  padding-right: 24px;
  padding-left: 19px;
  border-radius: 6px;
  font-size: inherit;
  background-color: #666666;
  background: linear-gradient(to right, #666666, #888888);
  color: white;
  cursor: pointer;
  height: 30px;
  box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.14), 0 1px 1px 0 rgba(0, 0, 0, 0.12), 0 1px 1px -2px rgba(0, 0, 0, 0.2);
}

.printer-button.enabled {
  background-color: #0055a6;
  background: linear-gradient(to right, #0055a6, #2672bd);
}

.blue-button {
  padding-right: 24px;
  padding-left: 24px;
  border-radius: 6px;
  font-size: inherit;
  background-color: #0055a6;
  background: linear-gradient(to right, #0055a6, #2672bd);
  color: white;
  cursor: pointer;
  height: 30px;
  box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.14), 0 1px 1px 0 rgba(0, 0, 0, 0.12), 0 1px 1px -2px rgba(0, 0, 0, 0.2);
