.menu {
  display: flex;
  flex-direction: row;
}

.menu .menu-item {
  height: 40px;
  display: flex;
  flex: 1;
  align-items: center;
  justify-content: center;
  text-decoration: blink;
  background-color: #3f496c;
  padding-right: 16px;
  padding-left: 16px;
  font-weight: normal;
  color: white;
  cursor: pointer;
  font-size: 15px;
  -webkit-transition: top 1s ease-out 0.5s;
  -moz-transition: top 1s ease-out 0.5s;
  -o-transition: top 1s ease-out 0.5s;
  transition: top 1s ease-out 0.5s;
}

.menu .menu-item.disabled {
  color: grey;
  cursor: default;
}

.menu .user {
  height: 40px;
  display: flex;
  flex: 1;
  align-items: center;
  justify-content: center;
  background-color: #606986;
  padding-right: 16px;
  padding-left: 16px;
  font-weight: normal;
  color: white;
  cursor: default;
  font-size: 15px;
  white-space: nowrap;
}

.menu .menu-item.selected {
  color: #95ff98;
}

.menu .devider {
  width: 1px;
  background-color: white;
  display: block;
  min-width: 1px;
  max-width: 1px;
}

.content {
  padding-top: 24px;
  padding-right: 24px;
  padding-left: 24px;
  padding-bottom: 24px;
}

.content .menu-column {
  width: 210px;
  display: flex;
  flex-direction: column;
}

.left-menu {
  width: 210px;
  display: flex;
  flex: none;
  flex-direction: column;
  font-size: 15px;
}

.left-menu-item {
  padding-top: 8px;
  padding-right: 0;
  padding-left: 32px;
  padding-bottom: 8px;
  color: #535973;
  cursor: pointer;
  text-decoration: blink;
}

.left-menu-item:first-child {
  margin-top: 16px;
}

.left-menu-item:last-child {
  margin-bottom: 16px;
}

.left-menu-item.selected {
