  /* flex: none; */
  padding: 18px;
  flex-direction: column;
  background: #c8cdd3;
}

.label {
  font-weight: 600;
  margin-right: 8px;
  padding-bottom: 1px;
  color: #1c1d1f;
  text-align: left !important;
}

.field.disabled {
  color: #aaa;
}

.field.disabled .icon-clear {
  cursor: initial;
}

.field.disabled .icon-clear .path3:before {
  color: #aaa;
}

.table-layout {
  padding: 18px;
}

.last {
  width: 205px;
}

.first {
  width: 32px;
  cursor: pointer;
}

.header-row {
  min-height: 23px;
  background-color: #3e5567;
  color: white;
}

.row {
  min-height: 23px;
  margin-left: 0;
  margin-right: 0;
}

.row.expanded {
  background-color: #666666;
  border-top: 1px solid #666666;
}

.row.expanded .cell {
  border-left: 1px solid #666666;
}

.row.expanded .cell:last-child {
  border-right: 1px solid #666666;
}

.table {
  margin-bottom: 0;
}

.header-row,
.row {
  border-top: 1px solid #d2d2d2 !important;
}

.table > div:last-child > .row {
  border-bottom: 1px solid #d2d2d2;
}

.table > div:last-child > .inner-table > div:last-child .row {
  border-bottom: 1px solid #d2d2d2;
}

.table > div:not(:last-child) > div.expanded.row > .first.cell {
  border-bottom: 1px solid #666666;
}

.table > div:last-child > div:nth-child(2) {
  border-top: 0 solid white !important;
}

.header-cell,
.cell {
  border-left: 1px solid #d2d2d2;
  padding-left: 8px;
  padding-right: 8px;
}

.header-cell {
  font-weight: 600;
}

.header-cell.sortable {
  cursor: pointer;
}

.header-cell.sort-0:after {
  content: "";
  display: inline-block;
  width: 0;
  height: 0;
  margin-left: 2px;
  vertical-align: middle;
  border-top: 4px dashed;
  border-top: 4px solid \9;
  border-right: 4px solid transparent;
  border-left: 4px solid transparent;
}

.header-cell.sort-1:after {
  content: "";
  display: inline-block;
  width: 0;
  height: 0;
  margin-left: 2px;
  vertical-align: middle;
  border-bottom: 4px dashed;
  border-bottom: 4px solid \9;
  border-right: 4px solid transparent;
  border-left: 4px solid transparent;
}

.header-cell:last-child,
.cell:last-child {
  border-right: 1px solid #d2d2d2;
}

.header-cell.first,
.cell.first {
  padding-left: 0;
  width: 28px;
}

.header-cell.last,
.cell.last {
  padding-left: 0;
  width: 49px;
}

.cell {
  cursor: pointer;
  white-space: normal;
}

.add-link {
  color: #4f8cae;
  cursor: pointer;
}

.inner-table {
  padding-left: 29px;
}

.inner-table .header-row {
  border-top: 0px solid #d2d2d2;
}

.paginator-item {
  margin: 16px 16px 0 0;
  cursor: pointer;
}

.paginator-item.current-page {
  color: #7da7d2;
  font-weight: 600;
}

.tooltip {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 48px;
  font-size: 16px;
  cursor: pointer;
  opacity: 1;
}

.tooltip.info {
  color: #eeff00;
  background: rgba(14, 173, 35, 0.8);
}

.tooltip.error {
  color: #eeff00;
  background: rgba(173, 14, 14, 0.8);
}

.cell120px {
  max-width: 140px;
  font-weight: 600;
}

.grey-back {
  background-color: #d8d8d8;
}

.blue-card {
  background-color: #e3ebf6;
  padding: 8px;
}

.file {
  padding: 4px;
  margin: 2px;
  border-radius: 2px;
}

.file.grey {
  background: #d8d8d8;
}

.heightAuto {
  height: auto;
}

.no-border {
  border: 0;
}

.comment {
  min-height: 88px;
  overflow: auto;
}

.primary-layout {
  position: relative;
  margin-bottom: 32px;
}

.primary-layout .footer {
  z-index: 999;
  position: fixed;
  width: 100%;
  background-color: #fff;
  height: 32px;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 700;
  border-top: 1px solid #000;
}

.filter-date {
  font-family: 'Chinese Quote', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'PingFang SC', 'Hiragino Sans GB', 'Microsoft YaHei', 'Helvetica Neue', Helvetica, Arial, sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol';
  font-variant: tabular-nums;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  list-style: none;
  position: relative;
  display: inline-block;
  padding: 4px 11px;
  width: 100%;
  height: 32px;
  font-size: 14px;
  line-height: 1.5;
  color: rgba(0, 0, 0, 0.65);
  background-color: #fff;
  background-image: none;
  border: 0px solid #d9d9d9;
  border-radius: 4px;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}

.display-none {
  display: none;
}




