.login-container {
  background: #f6f7f8;
  width: 100%;
  height: 100%;
  position: absolute;
}

.main-login {
  width: 550px;
  height: 210px;
  position: absolute;
  top: 30%;
  left: 50%;
  margin-left: -225px;
  border: 2px solid #ecf3f8;
  border-left: 3px solid #ecf3f8;
}

.main-login .loading {
  position: absolute;
  left: 45%;
  top: 17%;
}

.main-login .main-form {
  padding: 0;
  border: 1px solid #e2e6ea;
  background: #fff;
}

.main-login .main-form .main-form-body {
  padding: 10px 15px;
  padding-bottom: 35px;
  position: relative;
}

.main-login .main-form .main-form-body .logo {
  width: 518px;
  height: 104px;
}

.main-login .main-form .main-form-body .title {
  margin-bottom: 16px;
}

.main-login .main-form .main-form-body .error-message {
  color: red;
  text-align: center;
}

.main-login .main-form .main-field {
  padding-bottom: 9px;
}

.main-login .main-form .main-field input,
.main-login .main-form .main-field input[type='password'] {
  padding: 3px 2px;
  border: solid 1px #cceff4;
  border-left: 1px solid #aacfe4;
  border-top: 1px solid #aacfe4;
}

.main-login .main-form .main-field input.error,
.main-login .main-form .main-field input[type='password'].error {
  border-color: red;
}

.main-login .main-form h2 {
  font-size: 16rem;
}

.main-login .main-form .form-btn {
  position: relative;
  bottom: 1px;
  width: 100%;
  left: 0px;
  height: 38px;
  padding-top: 5px;
}

.main-login .main-form .form-btn button {
  float: right;
  margin-right: 4px;
}

.main-login .bottom-text {
  margin-top: 10px;
}




