.button {
  min-width: 90rem;
  padding: 0 12rem;
  border-radius: 0;
}

.b-file .icon-folder_open {
  position: relative;
  left: -4px;
  top: 2px;
}

.button {
  font-size: 14rem;
  font-family: Calibri, Arial, sans-serif;
  font-weight: 500;
  line-height: 1;
  letter-spacing: 0;
  position: relative;
  display: inline-block;
  height: 26rem;
  flex-direction: row;
  align-content: center;
  align-items: center;
  justify-content: center;
  line-height: 26rem;
  text-align: center;
  text-decoration: none;
  white-space: nowrap;
  cursor: pointer;
  border: 0;
  outline: none;
  transition: box-shadow 0.2s cubic-bezier(0.4, 0, 1, 1), background-color 0.2s cubic-bezier(0.4, 0, 0.2, 1), color 0.2s cubic-bezier(0.4, 0, 0.2, 1);
  /*> span:not([data-react-toolbox="tooltip"]) {
    display: inline-block;
    line-height: $button-height;
    vertical-align: middle;
  }*/
}

.button::-moz-focus-inner {
  border: 0;
}

.button > svg {
  display: inline-block;
  width: 1em;
  height: 26rem;
  font-size: 120%;
  vertical-align: middle;
  fill: currentColor;
}

.button > * {
  pointer-events: none;
}

.button > .rippleWrapper {
  overflow: hidden;
}

.button[disabled] {
  color: rgba(0, 0, 0, 0.26) !important;
  background: #c7d1de !important;
  pointer-events: none;
  cursor: auto;
}

.menu-drop-down {
  background: white;
  position: absolute;
  z-index: 100;
  padding: 4px;
  border: solid #86a9cc 1px;
  top: 100%;
  left: 1px;
  display: none;
  width: 100%;
}

.menu-drop-down.open {
  display: block;
}

.menu-drop-down li {
  padding: 2px 3px;
  font-size: 13rem;
  font-weight: normal;
  vertical-align: middle;
  min-width: 150rem;
}

.menu-drop-down li:hover {
  background-color: #eee;
}

.button {
  position: relative;
}

.button.dd:after {
  content: "";
  display: inline-block;
  width: 0;
  height: 0;
  margin-left: 2px;
  vertical-align: middle;
  border-top: 4px dashed;
  border-top: 4px solid \9;
  border-right: 4px solid transparent;
  border-left: 4px solid transparent;
}

.button.dd .text {
  display: inline-block;
  width: 96%;
}

.button.dd.btn-menu {
  position: relative;
  right: 0;
  left: inherit;
  width: 100%;
  color: black;
  padding: 0;
}

.button.dd:hover {
  opacity: 1 !important;
}

.button .loading {
  position: absolute;
  left: -26px;
  top: 1px;
}

.button.btn-icon {
  min-width: auto;
}

.button:not([disabled]) {
  color: white;
  background: linear-gradient(to right, #0055a6, #2672bd);
}

.button:not([disabled]):hover {
  opacity: 0.8;
}

.button.red:not([disabled]) {
  color: white;
  background: linear-gradient(to right, #a60000, #bd2626);
}

.button.red:not([disabled]):hover {
  opacity: 0.8;
}




