.check {
  position: relative;
  display: inline-block;
  width: 14rem;
  height: 14rem;
  vertical-align: top;
  cursor: pointer;
  border-color: black;
  border-style: solid;
  border-width: 1px;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 0.2s;
  transition-property: background-color;
}

.check.checked {
  background-color: #ffffff;
}

.check.checked:after {
  position: absolute;
  top: 0rem;
  left: 3rem;
  width: 6rem;
  height: 10rem;
  content: "";
  border-color: #1a96c6;
  border-style: solid;
  border-top: 0;
  border-right-width: 2px;
  border-bottom-width: 2px;
  border-left: 0;
  transform: rotate(45deg);
}

.disabled .text,
.disabled .field-title {
  color: rgba(0, 0, 0, 0.6);
}

.disabled .check {
  cursor: auto;
  border-color: rgba(0, 0, 0, 0.6);
  background-color: rgba(0, 0, 0, 0.1);
  cursor: not-allowed;
}

.disabled .check.checked {
  cursor: auto;
}

@keyframes checkmark-expand {
  0% {
    top: 9rem;
    left: 6rem;
    width: 0;
    height: 0;
  }

  100% {
    top: -1rem;
    left: 4rem;
    width: 7rem;
    height: 12rem;
  }
}




