.overlay {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
  display: flex;
  width: 100vw;
  height: 100vh;
  flex-direction: column;
  align-content: center;
  align-items: center;
  justify-content: center;
  pointer-events: none;
  pointer-events: all;
}

.overlay-form {
  pointer-events: all;
  margin: 0 auto;
  width: 620px;
}

.overlay-form.inherit {
  width: inherit;
}

.overlay-form.o-1200 {
  width: 1200px;
}

.overlay-form.o-700 {
  width: 700px;
}

.overlay-form.o-700 .main-merge {
  width: 700px;
}

.overlay-form.o-360 {
  width: 360px;
}

.overlay-form.o-520 {
  width: 520px;
}

.overlay-form.o-520 .backdrop {
  z-index: 1100;
}

.overlay-form.o-520 .dialog-form {
  width: 520px;
  z-index: 1200;
}

.overlay-form.fix-body .body {
  padding: 0;
}

.overlay-form.fix-body .body .title {
  padding: 24rem 24rem 0 24rem;
}

.backdrop {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(96, 128, 172, 0.501961);
  opacity: 0;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 0.35s;
  transition-property: opacity;
}

.backdrop.active {
  opacity: 0.6;
}

.backdrop.fix {
  position: fixed;
  z-index: 999;
}




