.dialog-blok {
  padding: 2px;
  margin-top: 15px;
}

.g-field {
  position: relative;
  border-bottom: 1px solid #d5e2ec;
  padding: 2px 0;
}

.g-field .g-title {
  position: relative;
  font-size: 14px;
  font-weight: bold;
  color: black;
  width: 144px;
  display: inline-block;
}

.g-field .g-value {
  position: relative;
  color: #000;
}

#g-items-count {
  position: absolute;
  top: 8px;
  font-size: 12rem;
}

.dialog-load-panel p {
  font-size: 18px;
  padding: 5px;
}

.dialog-load-panel .spinner {
  position: relative;
  left: 44%;
  width: 25px;
}

.dialog-load-panel .spinner .spinner-icon {
  border-top-color: #29d;
  width: 25px;
  height: 25px;
  -webkit-animation: nprogress-spinner 800ms linear infinite;
  animation: nprogress-spinner 800ms linear infinite;
}

.dialog-error-message {
  padding: 15px;
  font-size: 18px;
}

.print-action {
  margin: 19px 0;
}

.print-action .progress {
  padding-left: 15px;
  font-weight: bold;
  color: #009688;
}

.print-action .print-complited {
  position: relative;
  background-color: #00d1b2;
  color: #fff;
  font-weight: bold;
}

.print-action .print-complited.error {
  background-color: red;
}

.print-action .print-complited p {
  position: relative;
  font-weight: bold;
  font-size: 16px;
  padding: 5px;
  text-align: center;
}




