.main-stantion-users {
  position: relative;
}

.main-stantion-users .main-add-us {
  position: relative;
}

.main-stantion-users .flex-grid .drop-down {
  max-height: 140px !important;
}

.main-stantion-users .flex-grid .item {
  flex-flow: row wrap;
  align-content: space-between;
  justify-content: space-between;
  padding: 0;
}

.main-stantion-users .flex-grid .item .g-user {
  position: relative;
  display: block;
  width: 100%;
  border-bottom: 1px solid #ccc;
  padding: 2px;
}

.main-stantion-users .flex-grid .item .g-user .icon-remov {
  position: absolute;
  font-size: 12px;
  right: 2px;
  top: 4px;
  color: #0458a9;
  cursor: pointer;
}

.main-stantion-users .flex-grid .item .g-user:last-child {
  border-bottom: none;
}

.g-nmpn .title {
  position: relative;
  text-align: center;
  padding-top: 13px;
  color: #3f496c;
  font-size: 16px;
  margin-bottom: 0;
}

.g-nmpn table.grid {
  position: relative;
  display: table;
  /* border-collapse: separate; */
  border-spacing: 2px;
  border-color: #808080;
}

.g-nmpn table.grid tr {
  position: relative;
}

.g-nmpn table.grid tr th {
  position: relative;
  min-width: 55px;
  text-align: center;
  font-weight: normal;
  vertical-align: middle;
  height: 22px;
  background: #3f496c;
  color: #000;
  border: none;
  border-bottom: 1px solid #ccc;
  border-top: 1px solid #ccc;
  padding: 5px;
  border-right: 1px solid #fff;
  width: 120px;
  color: #fff;
}

.g-nmpn table.grid tr th.hday {
  background: #9a1f1f;
}

.g-nmpn table.grid tr td {
  position: relative;
}

.g-nmpn table.grid tr td:first-child {
  position: relative;
  background: #596eca;
  color: #fff;
}

.g-nmpn table.grid tr td.g-item {
  position: relative;
}

.g-nmpn table.grid tr td.g-item.g-green {
  background: #ececec;
  cursor: not-allowed;
}

.g-nmpn table.grid tr td.g-item .icon-user {
  color: #3d4a6b;
  font-size: 16px;
  top: -2px;
  position: relative;
}

.g-nmpn table.grid tr td .g-npm-info {
  text-align: center;
  height: 22px;
}




