.g-day-of-week {
  display: flex;
  flex-direction: row;
}

.g-day-of-week span {
  position: relative;
  display: inline-block;
  height: 26px;
  display: flex;
  flex: 1;
  align-items: center;
  justify-content: center;
  text-decoration: blink;
  background-color: #3f496c;
  padding-right: 16px;
  padding-left: 16px;
  font-weight: normal;
  color: white;
  cursor: pointer;
  font-size: 15px;
  -webkit-transition: top 1s ease-out 0.5s;
  -moz-transition: top 1s ease-out 0.5s;
  -o-transition: top 1s ease-out 0.5s;
  transition: top 1s ease-out 0.5s;
}

.g-day-of-week span.activ {
  position: relative;
  background: #ca9f25;
}

.g-day-of-week span:first-child {
  position: relative;
}

.g-day-of-week span:last-child {
  position: relative;
}




